.videoFooter {
  position: relative;
  bottom: 130px;
  color: white;
  margin-left: 30px;
}
.videoFooter__text h3 {
  padding-bottom: 20px;
}
.videoFooter__text p {
  padding-bottom: 20px;
}
.videoFooter__icon{

}
.videoFooter__music {
  display: flex;
}
.videoFooterMusic_text {
  width: 82%;
  overflow-x: hidden;
}
.videoFooterMusic_text p{
  animation: moveTheText 6s infinite linear;

}
.videoFooter__record {
    position: absolute;
    bottom: 7%;
    right: 25px;
    height: 40px;
filter: invert(1);
animation: spinTheRecord 3s infinite linear;

}

@keyframes spinTheRecord{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

@keyframes moveTheText{
  0%{
    transform: translateX(100%);
}
100%{
  transform: translateX(-100%);
}
}

@media only screen and (max-width: 400px){
  .videoFooter {
    bottom: 22%;
    
  }
  .videoFooterMusic_text {
    width: 78%;
    
  }
}